/**/
/* Primary / Secondary Sidebars 
------------------------------------------------------------ */
.sidebar_container {
  display: none;
  position: absolute;
  overflow-y: auto;
  top: 0;
  right: 0;
  padding-top: 50px;
  padding-top: 3.125rem;
  height: 100%;
  background-color: #333;
}
.sidebar_container:not(:target) {
  z-index: 999;
  height: 0;
}
.sidebar_container:target .close-btn {
  display: block;
}
.sidebar_container .sidebar_container_tittle_wrapper {
  height: 20px;
  float: left;
  width: 100%;
  background-color: #D68729;
}
.sidebar_container .sidebar_container_tittle {
  position: relative;
  bottom: 35px;
  font-size: 0.9rem;
  margin: 0 auto;
  width: 70px;
  color: #8C8B8B;
}
.sidebar_container input[type="search"] {
  border-left: none;
  border-right: none;
}
.sidebar_container .sidebar {
  width: 100%;
}
.sidebar_container .sidebar .widget_search input[type="search"] {
  background-color: #fff;
}
.sidebar_container .sidebar .widget-wrap ul a, .sidebar_container .sidebar .highlight {
  border-bottom: 1px solid #262626;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  padding: 0.66667rem;
  -webkit-transition: background 300ms ease;
          transition: background 300ms ease;
}
.sidebar_container .sidebar .widget-wrap ul a:hover {
  background: #242424;
  text-decoration: none;
}
.sidebar_container .sidebar .highlight {
  padding: 0.3rem 0.83333rem;
}
.sidebar_container .sidebar .contact-hours-footer {
  list-style: none;
  margin: 0;
}
.sidebar_container .sidebar .contact-footer-icon {
  width: 1.2rem;
}
.sidebar_container .close-btn {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0px;
  width: 26px;
  width: 2.625em;
  height: 26px;
  height: 2.6em;
  float: right;
  padding: 0;
  border: 0;
  outline: none;
  background: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/close-btn.svg") center center no-repeat transparent;
  background-size: 1.875em 1.875em;
  overflow: hidden;
  white-space: nowrap;
  text-indent: 100%;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
  -webkit-tap-highlight-color: transparent;
}

.js-ready #sidebar_container {
  height: 100%;
  display: block;
}
.js-ready #sidebar_container .close-btn {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
}

.js-ready #sidebar_container .close-btn:focus, .js-ready #sidebar_container .close-btn:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.sidebar-content .sidebar, .sidebar-sidebar-content .sidebar {
  float: left;
}

.sidebar .widget-wrap > ul, .sidebar .menu {
  margin: 0;
  list-style: none;
}

.sidebar p {
  font-size: inherit;
  text-align: left;
}

.sidebar .widget {
  word-wrap: break-word;
  float: left;
  margin: 0;
  padding: 0;
  width: 100%;
}

.sidebar .widget:first-child {
  margin-top: 0;
}

.sidebar span.post-date {
  float: left;
  clear: both;
}

.sidebar .entry-header, .sidebar .entry-content {
  padding: 0;
}

.sidebar .featuredpost .post, .sidebar .featuredpage .page {
  background: none;
  padding: 0;
  width: 100%;
}

.sidebar .featuredpost .post:last-child, .sidebar .featuredpage .page:last-child {
  border: medium none;
  margin: 0;
}

.sidebar .entry-image.attachment-post {
  height: 66px;
  width: 66px;
}

.sidebar .entry-content p {
  text-align: left;
  line-height: 22px;
  padding: 0;
}

.sidebar .entry a.more-link, .footer-widgets .entry a.more-link {
  background-color: transparent;
  border: none;
  color: #FF304E;
  display: inline;
  font-weight: 400;
  letter-spacing: 0;
  margin: 0;
  padding: 0;
  text-transform: capitalize;
  vertical-align: baseline;
}

.sidebar .entry a.more-link:hover, .footer-widgets .entry a.more-link:hover {
  background-color: transparent;
  color: #131212;
  text-decoration: underline;
}

.sidebar .entry-meta {
  padding: 10px 0;
  text-align: left;
}

.featured-content .entry {
  border-bottom: 2px dotted #131212;
  margin-bottom: 30px;
  margin-bottom: 3rem;
}

/* Hidden Sidebar Style
--------------------------------------------------------------*/
#site-wrapper {
  width: 100%;
}

.nav-btn {
  cursor: pointer;
  display: block;
  width: 42px;
  width: 2.625rem;
  height: 40px;
  height: 2.5rem;
  float: right;
  padding: 0;
  border: 0;
  outline: none;
  background: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/nav-icon.svg") no-repeat scroll center center #131212;
  background-size: 30px 24px;
  background-size: 1.875rem 1.5rem;
  overflow: hidden;
  white-space: nowrap;
  text-indent: 100%;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=70);
  opacity: 0.7;
  -webkit-tap-highlight-color: transparent;
  position: absolute;
  right: 0px;
  top: 0;
  z-index: 50;
  -webkit-transition: all 1s;
          transition: all 1s;
}

.js-nav .nav-btn {
  background: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/close-btn.svg") no-repeat scroll center center #131212;
}

.no-svg.js-nav .nav-btn {
  background: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/close-btn.png") no-repeat scroll center center #131212;
}

.no-svg .nav-btn {
  background-image: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/nav-icon.png");
}

.home .nav-btn {
  display: none;
}

.nav-btn:hover, .nav-btn:focus {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.no-svg .close-btn {
  background-image: url("https://489323192bc07f331d47-c06041d322f1ad6b39306f4da82d1689.ssl.cf1.rackcdn.com/wp-content/themes/argo/images/close-btn.png");
}

.close-btn:focus, .close-btn:hover {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  opacity: 1;
}

.sidebar h2.entry-title a, .footer-widgets h2.entry-title a {
  color: #131212;
}

.sidebar h2.entry-title a:hover, .footer-widgets h2.entry-title a:hover {
  color: #FF304E;
  text-decoration: underline;
}

.widget-area h4 {
  /*font-size: 16px;
  font-size: 1.6rem;
  font-weight: 400;
  border-bottom: 2px solid #131212;
  margin: 0 0 30px;
  margin: 0 0 3rem;
  text-transform: uppercase;*/
  background: #444444;
  border-bottom: none;
  border-top: 1px solid #5e5e5e;
  color: #999999;
  display: block;
  font-size: 0.66667rem;
  font-weight: bold;
  margin: 0;
  padding: 0.3rem 0.83333rem;
  text-transform: uppercase;
}

.csstransforms3d.csstransitions.js-ready #sidebar_container {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -webkit-backface-visibility: hidden;
  -ms-backface-visibility: hidden;
  -o-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transform: translate3d(100%, 0, 0) scale3d(0.01, 1, 1);
          transform: translate3d(100%, 0, 0) scale3d(0.01, 1, 1);
  -webkit-transition: opacity 300ms 100ms ease, -webkit-transform 500ms ease;
          transition: opacity 300ms 100ms ease, transform 500ms ease;
}

.csstransforms3d.csstransitions.js-nav #sidebar_container {
  -webkit-transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
          transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
  opacity: 1;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transition: opacity 300ms 100ms ease, -webkit-transform 500ms ease;
          transition: opacity 300ms 100ms ease, transform 500ms ease;
}

@media only screen and (min-width: 40.0625em) {
  .sidebar_container .sidebar {
    width: 360px;
  }
}


